body {
  font-size: 14px;
  letter-spacing: 0.5px;
  overflow-x: hidden;
  font-family: var(--bs-font-sans-serif);
  background: #f6f7fa;
}
a {
  text-decoration: none;
}
.wrapper {
  width: 100%;
  position: relative;
}
.sidebar-wrapper {
  width: 250px;
  height: 100%;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  background: #fff;
  border-right: 0 solid #e4e4e4;
  z-index: 11;
  box-shadow: 0 0 2rem 0 #e6e6e6;
  transition: all 0.2s ease-out;
}
.sidebar-header {
  width: 250px;
  height: 60px;
  display: flex;
  align-items: center;
  position: absolute;
  top: 0;
  bottom: 0;
  padding: 0 15px;
  z-index: 5;
  background: #fff;
  background-clip: border-box;
}
.small-logo {
  width: 100%;
  display: none;
  margin-left: 0 !important;
}
.logo-icon {
  width: 100%;
  margin-left: -11px;
  margin-top: 6px;
}
.logo-text {
  font-size: 22px;
  margin-left: 5px;
  margin-bottom: 0;
  letter-spacing: 1px;
  color: var(--bs-secondary);
  font-weight: 700;
}
.toggle-icon {
  font-size: 22px;
  cursor: pointer;
  color: var(--bs-primary);
}
.topbar {
  position: fixed;
  top: 0;
  left: 250px;
  right: 0;
  height: 60px;
  background: #fff;
  border-bottom: initial;
  background-clip: border-box;
  z-index: 10;
  color: #525f7f;
}
html.light-theme .topbar {
  box-shadow: 0 0.125rem 0.25rem #ededed !important
}
.topbar .navbar {
  width: 100%;
  height: 60px;
  padding-left: 30px;
  padding-right: 30px;
}
.page-wrapper {
  height: 100%;
  margin-top: 60px;
  margin-bottom: 30px;
  margin-left: 250px;
}
.page-content {
  padding: 1.5rem;
}
.page-footer {
  background: #fff;
  left: 250px;
  right: 0;
  bottom: 0;
  position: fixed;
  text-align: center;
  padding: 7px;
  font-size: 12.5px;
  z-index: 3;
  box-shadow: 2px 7px 2rem 0 red;
}
.wrapper.toggled:not(.sidebar-hovered) .logo-icon {
  display: none;
}
.wrapper.toggled:not(.sidebar-hovered) .small-logo {
  display: inline-block;
}
.wrapper.toggled .topbar {
  left: 70px;
}
.wrapper.toggled .page-wrapper {
  margin-left: 70px;
}
.wrapper.toggled .page-footer {
  left: 70px;
}
.sidebar-wrapper ul {
  padding: 0;
  margin: 0;
  list-style: none;
  background: transparent;
}
.sidebar-wrapper .metismenu {
  background: transparent;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 10px;
  padding-top: 0;
  margin-top: 70px;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
}
.sidebar-wrapper .metismenu li + li {
  margin-top: 5px;
}
.sidebar-wrapper .metismenu li:last-child {
  margin-bottom: 5px;
}
.sidebar-wrapper .metismenu > li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  position: relative;
}
.sidebar-wrapper .metismenu a {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: left;
  padding: 8px;
  font-size: 12px;
  color: var(--bs-secondary);
  outline-width: 0;
  text-overflow: ellipsis;
  overflow: hidden;
  letter-spacing: 0.5px;
  border: 1px solid #ffffff00;
  transition: all 0.3s ease-out;
  font-weight: 600;
  border-radius: 3px;
}
.sidebar-wrapper .metismenu a .parent-icon {
  font-size: 18px;
  line-height: 1;
}
.sidebar-wrapper .metismenu a .menu-title {
  margin-left: 10px;
}
.sidebar-wrapper .metismenu ul a {
  padding: 9px 15px;
  font-size: 12px;
  border: 0;
  border-radius: 0 0 3px 3px;
}
.mm-collapse {
  margin-top: -2px;
  display: none;
}
.sidebar-wrapper .metismenu ul a i {
  margin-right: 10px;
}
div.mdb-datatable div.mdb-datatable-info {
  font-weight: 600 !important;
}
